<template>
<div class="login-view">
  <div class="login-panel">
    <mu-text-field v-model="username" label="" hintText="" :errorText="usernameError" type="text" fullWidth icon="person" labelFloat/><br/>
    <mu-text-field v-model="password" label="" hintText="" :errorText="passwordError" type="password" fullWidth icon="lock" labelFloat/><br/>
    <captcha></captcha>
    <div class="button">     
      <mu-raised-button @click="doLogin" label="" class="login-button" primary/>
      <mu-raised-button @click="forgot" label="" class="forgot-button"/>
    </div>
  </div>
</div>
</template>

<script>
import userApi from '../api/userApi'
import MD5 from 'MD5';
import Captcha from '../components/common/Captcha'
import axios from 'axios'

export default {
  name: 'login-view',
  components: { Captcha },
  beforeCreate () {
    // 
    this.$store.dispatch('setCaptcha', '')
    this.$store.dispatch('setCaptchaError', '')
    // 
    if (this.$store.state.user.login) {
      // replace
      this.$router.replace('/')
    }
//  document.getElementsByClassName('header').CSS('visibility','none')
  },
  data () {
    return {
      username: '',
      password: '',
      usernameError: '',
      passwordError: '',
      failCount:''
    }
  },
  methods: {
    forgot () {
//  	if (this.username === '') {
//      this.usernameError = ''
//    }
//  	if(this.usernameError === ''){
//  		var user = {
//          		'logName' : this.username
//          	}
//       window.console.log(user)
//       this.$store.dispatch('forgot', user)
//  		 this.$router.push('/forgot')
//  	}
 			this.$store.dispatch('doLogin')
     	this.$router.push('/register')
    },
    doLogin () {
      if (this.username === '') {
        this.usernameError = ''
      }
      if (this.password === '') {
        this.passwordError = ''
      }
      if (this.$store.state.captcha.captcha.length !== 4) {
        this.$store.dispatch('setCaptchaError', '4')
      }
      if (this.usernameError === '' &&
          this.passwordError === '' &&
          this.$store.state.captcha.captchaError === '') {
        var data = {
          'logName': this.username,
					'logPWD': MD5(this.password).toString(),
          'verCode': this.$store.state.captcha.captcha
        }

        var _this = this
        userApi.login(data)
		.then(function (res) {
					//
          if (res.data['msgCode'] == '00001') {
            _this.$alert(res.data['message'], '', {
            	confirmButtonText: ''
            })
            //
            //this.$store.state.captcha.captchaError = res.data['message']
            // 
            //_this.$store.dispatch('setCaptcha', '')
            //_this.$store.dispatch('setCaptchaError', '')
            // 
            //_this.$store.dispatch('changeCaptcha')
            
          } 
          //
         if(res.data['msgCode'] === '00002'){
          	_this.$alert(res.data['message']+' '+res.data['failCount']+''+(5-res.data['failCount'])+'!', '', {
            	confirmButtonText: ''
            })
          	 // 
            //_this.username = ''
             // 
            //_this.password = ''
             //
           // _this.failCount =+ 1
            // 
            //_this.$store.dispatch('setCaptcha', '')
            //_this.$store.dispatch('setCaptchaError', '')
            // 
            //_this.$store.dispatch('changeCaptcha')
          }
         //5 
         if (res.data['msgCode'] == '00017') {
            _this.$alert(res.data['message'], '', {
            	confirmButtonText: ''
            })
            //
            //this.$store.state.captcha.captchaError = res.data['message']
            // 
           // _this.$store.dispatch('setCaptcha', '')
            // 
            //_this.$store.dispatch('changeCaptcha')
            
          } 
          //
          if(res.data['msgCode'] === '00000')  {
            //
            if(res.data['firstLogin']==0){
            	 _this.$router.push('/accounts/user-reset-password')
            	 var user = {
            		'logName' : res.data['logName']
            	}
			         _this.$store.dispatch('store', user)   
			         window.console.log(_this.$store.state.user.storeUser.logName)
            }else{
            //
            var data = {
            	'logName' : res.data['logName']
            }
              userApi.queryAccount(data)
					      .then(function (res) {
					      	if(res.data['msgCode'] === '00000'){
					      		var  account = {
					      			'id':res.data['id'],
					      			'companyName':res.data['componyName'],
					      			'componyShort':res.data['componyShort'],
					      			'address':res.data['address'],
					      			'connUser':res.data['connUser'],
					      			'telephone':res.data['telephone'],
					      			'email':res.data['email']
					      		} 
					      		_this.$store.dispatch('account', account)
					      		 window.console.log("account======="+_this.$store.state.user.account.connUser)
					      	}
					        
					      })
            var user = {
            		'logName' : res.data['logName'],
            		'userName':res.data['userName'],
            		'orgName':res.data['orgName'],
            		'mobile':res.data['mobile'],
            		'failCount':res.data['failCount'],
            	}
             _this.$store.dispatch('doLogin', user)
             _this.$router.push('/')
              }
          }
          

          
        })
        .catch(function (res) {
          if (res instanceof Error) {
            _this.loading = false
      		  _this.$alert('...', '', {
		          confirmButtonText: '',
		          type:'error'
            })
          } else {
            _this.loading = false
      		  _this.$alert('...', '', {
		          confirmButtonText: '',
		          type:'error'
            })
          }
        })
      }
    }
  },
  watch: {
    username () {
      this.usernameError = ''
    },
    password () {
      this.passwordError = ''
    }
  }
}
</script>

<style lang="stylus" scoped>
/*.mu-raised-button-primary
  background-color #555*/
.login-view
  margin-top 50px
  h1
    background #864
  .login-panel
    width 300px
    height 300px
    margin 0 auto
    .button
      width 300px
      margin 0 auto
      .forgot-button
        margin-left 24px
      .login-button
        margin-left 76px



</style>



